import React, { useState, useEffect } from 'react'
import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField'
import Snackbar from '@material-ui/core/Snackbar'
import { makeStyles } from '@material-ui/core/styles'
import DeleteButton from './delete_button.js'
import { getCookie } from './cookies.js'

const host = process.env.STORYBOOK ? process.env.API : ''

const useStyles = makeStyles(theme => ({
  root: {
    background: theme.palette.grey[100],
    padding: theme.spacing(2)
  },
  img: {
    maxWidth: '100%',
  },
  button: {
    marginTop: theme.spacing(2)
  }
}))

const ImageInfo = ({img, onDelete}) => {
  const classes = useStyles()
  const [message, setMessage] = useState('')
  const [open, setOpen] = useState(false)
  const [old, setOld] = useState({})
  const [data, setData] = useState({})

  useEffect(()=>{
    if(!img.id) {
      return
    }
    let headers = {
      'X-CSRFToken': getCookie('csrftoken'),
    }
    if(process.env.STORYBOOK) {
      headers['Authorization'] = 'Basic ' + btoa("chen:mdian1927")
    }

    fetch(`${host}/api/attachments/${img.id}/`, {
      headers: headers
    }).then(res => res.json()).then(res => {
      const ress = {
        name: res.name,
        description: res.description,
        tags: res.tags
      }
      setData(ress)
      setOld(ress)
    })
  }, [img])

  const handleChange = (e) => {
    setData({...data, [e.target.name]:e.target.value})
  }

  const handleSubmit = () => {
    if(data === old) {
      // 
      return
    }
    let headers = {
        'Content-Type': 'application/json',
    }
    if(process.env.STORYBOOK) {
      headers['Authorization'] = 'Basic ' + btoa("chen:mdian1927")
    }
    fetch(`${host}/api/attachments/${img.id}/`, {
      method: 'patch',
      headers: headers,
      body: JSON.stringify(data)
    }).then(res => res.json()).then(res => {
      setOpen(true)
      setMessage('')
      setOld(data)
      console.log(res)
    }).catch(err => {
      console.log(err)
    })
  }

  // 
  const handleDelete = () => {
    let headers = {
        'Content-Type': 'application/json',
    }
    if(process.env.STORYBOOK) {
      headers['Authorization'] = 'Basic ' + btoa("chen:mdian1927")
    }
    fetch(`${host}/api/attachments/${img.id}/`, {
      method: 'delete',
      headers: headers
    }).then(res => {
      setOpen(true)
      setMessage('')
      onDelete(img)
    }).catch(err => console.log(err))
  }

  if(Object.keys(img).length <=0) {
    return null
  }

  return (
    <div className={classes.root}>
      <Snackbar
        open={open}
        message={message}
        onClose={()=>setOpen(false)}
        autoHideDuration={2000}
      />
      <Typography
        variant="h6"
        component="h5"
        gutterBottom></Typography>
      <img
        className={classes.img}
        src={host+img.source} />
      <Typography variant="body2" color="textSecondary">
        {img.created}
        <br />
        {img.width}x{img.height}
        <br />
        {img.size}KB
      </Typography>
      <TextField
        fullWidth
        margin="normal"
        label=""
        value={data.name ? data.name : ''}
        name="name"
        onChange={handleChange}
        onBlur={handleSubmit}
      />
      <TextField
        fullWidth
        margin="normal"
        multiline
        rows={4}
        label=""
        value={data.description ? data.description : ''}
        name="description"
        onChange={handleChange}
        onBlur={handleSubmit}
      />
      <TextField
        fullWidth
        margin="normal"
        disabled
        label="URL"
        value={img.source}
      />
      <TextField
        fullWidth
        margin="normal"
        label=""
        value={data.tags ? data.tags : ''}
        name="tags"
        onChange={handleChange}
        onBlur={handleSubmit}
        helperText={''}
      />
      <DeleteButton
        onConfirm={handleDelete}
        className={classes.button}
        size="small" />
    </div>
  )
}

export default ImageInfo
